.temp1 {
  background: linear-gradient(
    to bottom,
    #c2e59c,
    #bff1db,
    #eedcbc,
    #f0e4a2,
    #9bcaf1
  );
  background-size: 100%;
  background-repeat: no-repeat;
}

temp1:before {
  content: "";
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: -1;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.2),
    #21d4fd 75%
  );
}
